*{
    margin: 0;
}

body{
    font-family: sans-serif;
}

.modals {
    padding: 100px 37px;
    background: #e9e7e7;
    width: 450px;
    text-align: center;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    border-radius: 10px;
    z-index: 9999;
}

.Todomain{
    width: 400px;
    border: 1px solid silver;
    border-radius: 10px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 20px;

}

h1{
    font-size: 30px;
    text-align: center;
    color: silver;
}

.Todomain input{
    width: 100%;
    padding: 10px;
    box-sizing: border-box;
    margin: 11px 0px;
    outline: none;
    border: 1px solid silver;
    border-radius: 3px;
}

.Todomain ul{
    list-style: none;
    padding: 0;
}
.Todomain input::placeholder{
    color: silver;
}

.Todomain ul li{
    background-color: #f4f2f2;
    padding: 10px;
    border-radius: 3px;
    color: silver;
    display: flex;
    margin-bottom: 10px;
}

.Todomain ul li i{
    
    cursor: pointer;
    margin-left: 10px;
}

.Todomain ul li div{
    text-align: right;
    flex: 1;
}


